













.welcome{
  max-width:400px;
  margin-top: 60px;
 margin:0 auto;
 text-align: center;
 padding: 20px;
}
.welcome h2{
  font-size: 3rem;
}
.welcome button{
  margin:30px auto;
}


.chat{
max-width: 400px;
margin: 0 auto;
padding: 20px;
}
.chat h2{
  font-size: 2.6rem;
}
.chat span{
  font-size: 1rem;
}
.chat .time{
  display: block;
  font-size: .8rem;
}
.input-text-content{
  padding: 0px 20px 20px 20px !important;
}
.messages{
  max-height: 300px;
  overflow: auto;
}
.messages::-webkit-scrollbar {
  width: 3px;
}
.messages::-webkit-scrollbar-track {
  background: #ddd;
}
.messages::-webkit-scrollbar-thumb {
  background: #aaa;
}














































